html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: 'Roboto', sans-serif;
  margin: 0; }

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

button {
  padding: 0; }

a {
  text-decoration: none; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

body {
  position: relative; }

.tic-tac-toe {
  padding: 100px 0;
  max-width: 1000px;
  margin: 0 auto; }

.tic-tac-toe__title {
  margin-bottom: 50px;
  text-align: center;
  color: rgba(0, 0, 0, 0.86); }

.statistics {
  margin-bottom: 30px; }

.statistics__score {
  display: flex;
  justify-content: space-between;
  width: 400px;
  margin: 0 auto 20px; }

.statistics__player {
  width: 45%;
  transition: 0.3s;
  border-bottom: 1px solid #aaaaaa;
  border-radius: 5px; }
  .statistics__player--current {
    box-shadow: 0 2px 0 0 teal; }

.statistics__player-field {
  text-align: center;
  margin-bottom: 5px; }

.statistics__inner {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px; }

.statistics__img {
  display: block;
  width: 20px;
  height: 20px; }
  .statistics__img--cross {
    background-image: url("../img/cross.svg"); }
  .statistics__img--circle {
    background-image: url("../img/circle.svg"); }

.statistics__info {
  text-align: center; }

.statistics__status {
  position: relative; }
  .statistics__status::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -25px;
    transform: translateY(-50%);
    width: 20px;
    height: 20px; }
  .statistics__status--cross::after {
    background-image: url("../img/cross.svg"); }
  .statistics__status--circle::after {
    background-image: url("../img//circle.svg"); }

.game__playground {
  margin: 0 auto;
  border-spacing: 6px; }

.game {
  max-width: 800px;
  margin: 0 auto 50px;
  padding: 50px 0;
  border: 5px dashed teal;
  border-radius: 40px; }

.game__col {
  position: relative;
  width: 70px;
  height: 70px;
  padding: 8px;
  background-color: teal; }
  .game__col--history {
    width: 50px;
    height: 50px; }

.circle,
.cross {
  position: absolute;
  left: 8px;
  right: 8px;
  bottom: 8px;
  top: 8px;
  display: none; }
  .circle--active,
  .cross--active {
    display: block; }

.controls {
  display: flex;
  justify-content: center; }

.button {
  display: block;
  width: 250px;
  padding: 5px 0;
  transition: 0.3s;
  cursor: pointer;
  outline: none;
  border-radius: 30px;
  font-size: 24px; }

.restart {
  border: 2px solid teal;
  background-color: teal;
  color: #ffffff; }
  .restart:hover {
    border-color: rgba(0, 128, 128, 0.7);
    background-color: rgba(0, 128, 128, 0.7); }

.history {
  margin-right: 10px;
  border: 2px solid teal;
  background-color: transparent;
  color: teal; }
  .history:hover {
    border-color: rgba(0, 128, 128, 0.7);
    background-color: rgba(0, 128, 128, 0.7);
    color: #ffffff; }

.history-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  height: 80vh;
  padding: 50px 5%;
  overflow-y: scroll;
  transform: translate(calc(-50% - 2000px), -50%);
  transition: 0.7s ease-in-out;
  border: 3px dashed teal;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 0.9); }
  .history-popup--active {
    transform: translate(-50%, -50%); }
  .history-popup::-webkit-scrollbar {
    width: 0px; }

.history-popup__item {
  width: 20%;
  margin: 2.5%;
  text-align: center; }

.history-popup__close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 35px;
  height: 35px;
  border: none;
  background-color: transparent; }
  .history-popup__close::before, .history-popup__close::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 3px;
    cursor: pointer;
    background-color: teal; }
  .history-popup__close::before {
    transform: rotate(45deg); }
  .history-popup__close::after {
    transform: rotate(-45deg); }
